import {Switch,Route,Redirect} from 'react-router-dom';
import LazyLoad from '&/lazyload';

export default class RouteMode extends Component{
  render(){
    return(
      <div>
        <Switch>
          <Route path='/index/mode/borrow' component={LazyLoad(()=>import('../borrow'))}></Route>
          <Route path='/index/mode/management' component={LazyLoad(()=>import('../management'))}></Route>
          <Route path="/index/mode/mine" component={LazyLoad(()=>import('../mine'))}></Route>
          <Route path="/index/mode/bookadd" component={LazyLoad(()=>import('../bookadd'))}></Route>
          <Route path="/index/mode/bookdelete" component={LazyLoad(()=>import('../bookdelete'))}></Route>
          <Route path="/index/mode/bookclassify" component={LazyLoad(()=>import('../bookclassify'))}></Route>
          <Route path="/index/mode/bookchange/:id" component={LazyLoad(()=>import('../bookchange'))}></Route>
          <Route path="/index/mode/bookreader" component={LazyLoad(()=>import('../bookreader'))}></Route>
          <Route path="/index/mode/bookrepay" component={LazyLoad(()=>import('../bookrepay'))}></Route>
          <Route path="/index/mode/bookborrow" component={LazyLoad(()=>import('../bookborrow'))}></Route>
          <Route path="/index/mode/storeinfo" component={LazyLoad(()=>import('../storeinfo'))}></Route>
          <Route path="/index/mode/storenotice" component={LazyLoad(()=>import('../storenotice'))}></Route>
          <Route render={()=>(<Redirect to="/index/information"/>)}></Route>
        </Switch>
      </div>
    )
  }
}